import { supabase } from './supabase';
import type { Complaint, User } from '@/types/database';

export interface ComplaintWithDetails extends Complaint {
  assignedUser?: User;
  categoryName?: string;
  createdAt: string; // Add this field for dashboard compatibility
}

export const complaintService = {
  // Get complaints for a specific user
  async getUserComplaints(userId: string): Promise<{ data: ComplaintWithDetails[] | null; error: string | null }> {
    try {
      const { data: complaints, error } = await supabase
        .from('complaints')
        .select('*')
        .eq('user_id', userId)
        .order('created_at', { ascending: false });

      if (error) {
        console.error('Error fetching complaints:', error);
        return { data: null, error: 'Failed to fetch complaints' };
      }

      // Map created_at to createdAt for consistency with frontend expectations
      const complaintsWithFormattedDate = complaints?.map(complaint => ({
        ...complaint,
        createdAt: complaint.created_at
      })) || [];

      return { data: complaintsWithFormattedDate as ComplaintWithDetails[], error: null };
    } catch (error) {
      console.error('Unexpected error:', error);
      return { data: null, error: 'An unexpected error occurred' };
    }
  },

  // Get all complaints (for public dashboard)
  async getAllComplaints(limit: number = 50): Promise<{ data: ComplaintWithDetails[] | null; error: string | null }> {
    try {
      const { data: complaints, error } = await supabase
        .from('complaints')
        .select('*')
        .order('created_at', { ascending: false })
        .limit(limit);

      if (error) {
        console.error('Error fetching all complaints:', error);
        return { data: null, error: 'Failed to fetch complaints' };
      }

      // Map created_at to createdAt for consistency with frontend expectations
      const complaintsWithFormattedDate = complaints?.map(complaint => ({
        ...complaint,
        createdAt: complaint.created_at
      })) || [];

      return { data: complaintsWithFormattedDate as ComplaintWithDetails[], error: null };
    } catch (error) {
      console.error('Unexpected error:', error);
      return { data: null, error: 'An unexpected error occurred' };
    }
  },

  // Get a single complaint by ID
  async getComplaintById(complaintId: string): Promise<{ data: ComplaintWithDetails | null; error: string | null }> {
    try {
      const { data: complaint, error } = await supabase
        .from('complaints')
        .select('*')
        .eq('id', complaintId)
        .single();

      if (error) {
        console.error('Error fetching complaint:', error);
        return { data: null, error: 'Failed to fetch complaint' };
      }

      // Map created_at to createdAt for consistency with frontend expectations
      const complaintWithFormattedDate = {
        ...complaint,
        createdAt: complaint.created_at
      };

      return { data: complaintWithFormattedDate as ComplaintWithDetails, error: null };
    } catch (error) {
      console.error('Unexpected error:', error);
      return { data: null, error: 'An unexpected error occurred' };
    }
  },

  // Get complaint statistics for a user
  async getUserStats(userId: string): Promise<{ 
    total: number; 
    pending: number; 
    resolved: number; 
    avgResolutionTime: string;
    error: string | null;
  }> {
    try {
      const { data: complaints, error } = await supabase
        .from('complaints')
        .select('status')
        .eq('user_id', userId);

      if (error) {
        console.error('Error fetching stats:', error);
        return { total: 0, pending: 0, resolved: 0, avgResolutionTime: 'N/A', error: 'Failed to fetch statistics' };
      }

      const total = complaints.length;
      const pending = complaints.filter(c => c.status === 'pending' || c.status === 'in_progress').length;
      const resolved = complaints.filter(c => c.status === 'resolved').length;

      // Since there's no timestamp data in the schema, we'll show N/A for resolution time
      const avgResolutionTime = 'N/A';

      return { total, pending, resolved, avgResolutionTime, error: null };
    } catch (error) {
      console.error('Unexpected error:', error);
      return { total: 0, pending: 0, resolved: 0, avgResolutionTime: 'N/A', error: 'An unexpected error occurred' };
    }
  },

  // Get categories
  async getCategories(): Promise<{ data: any[] | null; error: string | null }> {
    try {
      const { data: categories, error } = await supabase
        .from('categories')
        .select('*')
        .order('name');

      if (error) {
        console.error('Error fetching categories:', error);
        return { data: null, error: 'Failed to fetch categories' };
      }

      return { data: categories, error: null };
    } catch (error) {
      console.error('Unexpected error:', error);
      return { data: null, error: 'An unexpected error occurred' };
    }
  },

  // Create a new complaint
  async createComplaint(complaintData: {
    title: string;
    description: string;
    category: string;
    location: string;
    priority?: string;
    location_lat?: number;
    location_lng?: number;
    image_url?: string;
  }, userId: string): Promise<{ data: Complaint | null; error: string | null }> {
    try {
      const { data: complaint, error } = await supabase
        .from('complaints')
        .insert({
          title: complaintData.title,
          description: complaintData.description,
          category: complaintData.category,
          location: complaintData.location,
          user_id: userId,
          status: 'pending',
          priority: complaintData.priority || 'medium',
          location_lat: complaintData.location_lat,
          location_lng: complaintData.location_lng,
          image_url: complaintData.image_url,
        })
        .select()
        .single();

      if (error) {
        console.error('Error creating complaint:', error);
        return { data: null, error: 'Failed to create complaint' };
      }

      return { data: complaint, error: null };
    } catch (error) {
      console.error('Unexpected error:', error);
      return { data: null, error: 'An unexpected error occurred' };
    }
  }
};
